/* StepCounter.css */

.step-counter {
    margin-top: 2rem;
    display: flex;
    align-items: center;
    gap: 1rem;
}

.step-counter label {
    font-size: 1.2rem;
    color: #444;
}

.step-counter input {
    padding: 0.5rem;
    font-size: 1rem;
    border-radius: 4px;
    border: 1px solid #ccc;
    width: 80px;
    text-align: center;
}

.dark-mode .step-counter label {
    color: white;
}

.dark-mode .step-counter input {
    background-color: #555;
    color: white;
    border: none;
}
