/* Navbar.css */

nav {
    background-color: #333;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.Content {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.Content h1 {
    margin: 0;
    font-size: 2rem;
    font-weight: bold;
}

.Content p {
    margin: 0;
    font-size: 1.2rem;
    color: #bbb;
}

.toggleSwitch {
    display: flex;
    align-items: center;
}

#themeSwitch {
    opacity: 0;
    width: 0;
    height: 0;
}

.switchLabel {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}

.switchLabel .switch {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: 0.4s;
    border-radius: 34px;
}

.switchLabel .switch:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: 0.4s;
    border-radius: 50%;
}

#themeSwitch:checked + .switchLabel .switch {
    background-color: #000000;
}

#themeSwitch:checked + .switchLabel .switch:before {
    transform: translateX(26px);
}
