/* Counter.css */

/* Common styles for both themes */
.Container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  min-height: 100vh;
  transition: background-color 0.3s, color 0.3s;
}

.Container h1 {
  font-size: 2rem;
  margin-bottom: 2rem;
  text-align: center;
}

.counter {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 2rem;
}

.button1, .resetButton {
  border: none;
  padding: 0.75rem 1.5rem;
  font-size: 1.5rem;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.3s, transform 0.3s;
}

.button1 {
  background-color: #555;
  color: white;
  margin: 0 1rem;
}

.button1:hover {
  background-color: #777;
  transform: scale(1.1);
}

.button1:active {
  background-color: #999;
}

.count {
  font-size: 2rem;
  font-weight: bold;
  margin: 0 1rem;
}

.resetButton {
  background-color: #d9534f;
  color: white;
  font-size: 1rem;
}

.resetButton:hover {
  background-color: #c9302c;
  transform: scale(1.05);
}

.resetButton:active {
  background-color: #ac2925;
}

/* Light mode styles */
.light-mode .Container {
  background-color: #f5f5f5;
  color: #333;
}

.light-mode .button1 {
  background-color: #555;
  color: white;
}

.light-mode .button1:hover {
  background-color: #777;
}

.light-mode .button1:active {
  background-color: #999;
}

.light-mode .resetButton {
  background-color: #d9534f;
  color: white;
}

.light-mode .resetButton:hover {
  background-color: #c9302c;
}

.light-mode .resetButton:active {
  background-color: #ac2925;
}

/* Dark mode styles */
.dark-mode .Container {
  background-color: #111111;
  color: white;
}

.dark-mode .button1 {
  background-color: #777;
  color: black;
}

.dark-mode .button1:hover {
  background-color: #999;
}

.dark-mode .button1:active {
  background-color: #bbb;
}

.dark-mode .resetButton {
  background-color: #c9302c;
  color: black;
}

.dark-mode .resetButton:hover {
  background-color: #ac2925;
}

.dark-mode .resetButton:active {
  background-color: #8a1c1b;
}
